.galleryPage {
  height: 100vh;
  width: 680px;
}

.galleryPage__wrapper {
  position: relative;
  padding: 20px;
  margin-top: 10px;
  border-radius: 20px;
  background: #FFF;
}

.galleryPage__navigationBlock {
  display: flex;
  align-items: center;
}

.galleryPage__backLinkBlock {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background: #FBE0DC;
}

.galleryPage__backLink {
  position: relative;
  top: 10px;
  left: 10px;
  display: block;
  width: 20px;
  height: 20px;
}

.galleryPage__backLink svg {
  fill: #FF868E;
}

.galleryPage__backLinkBlock:hover {
  background-color: #FF868E;
  cursor: pointer;
}

.galleryPage__backLinkBlock:hover svg {
  fill: #FFFFFF;
}

.galleryPage__nameOfPage {
  margin-left: 10px;
  width: 146px;
  height: 40px;
  border-radius: 10px;
  background: #FF868E;
  color: #FFF;
  text-align: center;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 40px;
  letter-spacing: 2px;
}

.upload {
  margin-left: auto;
  align-items: center;
  width: 143px;
  height: 40px;
  border-radius: 10px;
  background: #FBE0DC;
  color: #FF868E;
  text-align: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 40px;
  letter-spacing: 2px;
}

.upload svg {
  margin-right: 10px;
  fill: #FF868E;
}

.upload:hover {
  cursor: pointer;
  background: #FF868E;
  color: #FFF;
}

.upload:hover svg {
  fill: #FFF;
}

.selectBlock {
  margin-top: 20px;
  padding: 0 20px 20px;
  height: 156px;
  border-radius: 20px;
  background: #F8F8F7;
}

.selectBlock h6 {
  margin: 0;
  padding-left: 10px;
  color: #8C8C8C;
  text-transform: uppercase;
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.selectBlock__item {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.selectOrder {
  width: 290px;
  padding: 8px 10px;
  height: 40px;
  border: 2px solid transparent;
  border-radius: 10px;
  color: #1D1D1D;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  background: #FFF;
}

.selectType {
  width: 290px;
  padding: 8px 10px;
  height: 40px;
  border: 2px solid transparent;
  border-radius: 10px;
  color: #1D1D1D;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  background: #FFF;
}

.selectBreed {
  width: 290px;
  height: 40px;
  border-radius: 10px;
  border: 2px solid transparent;
  background: #FFF;
  color: #1D1D1D;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  padding: 8px 10px;
}

.selectLimit {
  width: 240px;
  height: 40px;
  border-radius: 10px;
  border: 2px solid transparent;
  background: #FFF;
  color: #1D1D1D;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  padding: 8px 10px;
}

.selectOrder:hover,
.selectType:hover,
.selectBreed:hover,
.selectLimit:hover {
  outline: 2px solid #FBE0DC;
  cursor: pointer;
}

.loadBtn {
  position: relative;
  margin-top: 18px;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  border: none;
  background: #FFF;
}

.loadBtn svg {
  fill: #FF868E;
  position: absolute;
  top: 10px;
  left: 11px;
}

.loadBtn:hover {
  cursor: pointer;
  background: #FF868E;
}

.loadBtn:hover svg {
  fill: #FFF;
}

@media all and (max-width: 1290px) {
  .galleryPage {
    width: 520px;
  }

  .selectOrder, .selectType {
    width: 200px;
  }

  .selectBreed, .selectLimit {
    width: 180px;
  }
}

@media all and (max-width: 980px) {
  .galleryPage {
    margin-top: 80px;
    width: 728px;
  }

  .selectOrder,
  .selectType {
    width: 290px;
  }
  
  .selectBreed {
    width: 290px;
  }

  .selectLimit {
    width: 240px;
  }
}

@media all and (max-width: 750px) {
  .galleryPage {
    margin: 80px auto 0;
    width: 100%;
  }
}

@media all and (max-width: 700px) {
  .selectBlock {
    height: fit-content;
  }

  .selectOrder {
    margin-right: 10px;
    margin-bottom: 10px;
  }

  .selectBreed {
    margin-right: 10px;
  }

  .selectLimit {
    margin: 0 10px
  }
  
  .selectBlock__item {
    flex-wrap: wrap;
  }
}



@media all and (max-width: 660px) {
  .selectBreed {
    margin-bottom: 10px;
  }

  .selectLimit {
    margin-left: 0;
  }

  .galleryPage__navigationBlock {
    flex-wrap: wrap;
  }
    
  .galleryPage__nameOfPage {
    margin-right: 10px;
  }

  .upload {
    margin-left: 0;
  }
}

@media all and (max-width: 425px) {
  .galleryPage__wrapper {
    padding: 20px 10px;
  }
}

@media all and (max-width: 389px) {
  .upload {
    margin-top: 10px;
  }
}

@media all and (max-width: 380px) {
  .selectBreed,
  .selectLimit,
  .selectOrder,
  .selectType {
    width: 100%;
  }

  .selectType {
    margin: 0;
  }
}